@import '../../styles/base';

.home {
    display: flex;
    flex-direction: column;

    .page {
        display: flex;
        height: 100vh;
        flex-direction: column;
    }

    .welcome {
        background-color: $primary-color;
        justify-content: center;
        align-content: center;

        .logo {
            height: initial;
            width: 250px;
            max-width: 40%;
            align-self: center;
            margin-bottom: 10px;
        }

        .border {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            border: 10px solid black;
            margin: 10px;
        }
    }
}
